.cookies {
  background-color: #3c3c3c;
  position: fixed;
  bottom: -200px;
  left: 0;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin: 0 4.2vw;
  padding: 25px 0;
  display: flex;
  z-index: 2;
}
.cookies.active {
  bottom: -200px;
  transition: 0.3s;
  animation: cookies 0.5s forwards ease-out;
  animation-delay: 1s;
}
.cookies .text {
  width: 73%;
  padding: 0 59px;
  position: relative;
}
.cookies .text:after {
  position: absolute;
  top: -15px;
  right: 0;
  width: 1px;
  height: calc(100% + 33px);
  background-color: #535353;
  content: "";
}
.cookies .text p {
  color: #787878;
  font-weight: 500;
  font-size: 15px;
  line-height: 167%;
  margin: 0;
}
.cookies .btn {
  width: 27%;
  text-align: right;
  padding: 0 89px 0 20px;
}
.cookies .btn a {
  position: relative;
  color: #787878;
  font-size: 18px;
  font-weight: 600;
  letter-spacing: 0.257143px;
  line-height: 22px;
  padding-right: 49px;
}
.cookies .btn a:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 36px;
  height: 36px;
  margin: auto;
  background-image: url(../img/icons/slider-arrow-right-gray.svg);
  content: "";
}
@keyframes cookies {
  from {
    bottom: -200px;
  }
  to {
    bottom: 30px;
  }
}
@media screen and (max-width: 1199px) {
  .cookies .text {
    width: 73%;
    padding: 0 30px;
  }
  .cookies .text p {
    font-size: 12px;
